import React, { Component } from 'react';
import classes from './user.css';
import Aux from '../HOCs/Aux';
import Input from './input';
import Button from './button';

class User extends Component {

		state = {
				value: 'Akshay',
				age: 25,
				clicks: 0,
				user_details: []
		}

		onChangeHandler = (event) => {
    		this.setState({
    				value: event.target.value
    		});
    }

    onClickHandler = (event) => {
        this.setState({
						clicks: this.state.clicks + 1
        });
    }

    onSubmitHandler = (event) => {

        const updated_user_details = [
            ...this.state.user_details
        ];

        updated_user_details.push(
            {
                name: this.state.value,
                age: this.state.age
            }
        )

        this.setState({
						user_details: updated_user_details
        });
    }

    onDeleteHandler = (user_index) => {
				const delete_user_details = [
						...this.state.user_details
				];

				delete_user_details.splice(user_index, 1);

				this.setState({
						user_details: delete_user_details
				});
    }

		render() {

				console.log('props', this.props);

				const user_details = Object.keys( this.state.user_details )
	        .map( userKey => {
	            return (
	                <tr key={userKey}>
	                    <td> {this.state.user_details[userKey].name} </td>
	                    <td> {this.state.user_details[userKey].age} </td>
	                    <td> <Button name='x' clicked={ () => this.onDeleteHandler(userKey) } /> </td>
	                </tr> );
        } );

		    return (
		        <Aux>
		          <div className='user'>
		              This is a user. <br />
		              My Name is {this.state.value} <br />
		              <Button name='Click Me!' clicked={ (event) => this.onClickHandler(event) }/>
		              <br />
		              <strong> Total Clicks: {this.state.clicks} </strong>

										<Input type="text" name="name" changed={ (event) => this.onChangeHandler(event) }/>
										<Input type="text" name="age"/>
										<Button name="submit" clicked={ (event) => this.onSubmitHandler(event) } />

		              <table>
		                <thead>
		                  <tr>
		                    <th>Name</th>
		                    <th>Age</th>
	                    </tr>
		                </thead>
		                <tbody>
												{user_details}
		                </tbody>
		              </table>
		          </div>
		        </Aux>
		    );
    }

}


export default User;


