import React, { Component } from 'react';
import { Route, NavLink, Switch, Redirect, Link } from 'react-router-dom';
import Aux from '../HOCs/Aux';
import asyncComponent from '../HOCs/asyncComponent';
import axios from '../axios';
import classes from './user.css';
import MovieCards from './movie/movieCards';

const AsyncUser = asyncComponent(() => {
    return import('./User');
});


class Users extends Component {

		state = {
				posts : []
		}

    componentDidMount () {
        axios.get('/3/discover/movie?api_key=65324ba8898442570ac397a61cfa7f22&primary_release_year=2017&sort_by=revenue.desc')
            .then( response => {
                console.log('response.data', response.data);
                const posts = response.data;
                this.setState({
                    posts : response.data
                });
                console.log(posts);
            })
            .catch( error => {
                console.log( error );
                // this.setState({error: true});
            });
    }

		render() {
				let movie_result = <div className="loader"></div>;

				const param_list = [];

				const handleNewRoute = () => {
						param_list.push({    // use push
						   pathname: '/movie_details/',
						   query: this.state
						});
        }

        if ( !this.state.error ) {
            /*posts = this.state.posts.map( post => {
                return (
                    <MovieCards data={post} />
                );
            });*/

						const movie_details = this.state.posts;
						console.log('===', movie_details);
						const page = movie_details.page;
						const total_results = movie_details.total_results;
						const total_pages = movie_details.total_pages;
						const results = movie_details.results;


						if (results) {
								movie_result = results.map ( result => {
										return (
												<Aux key={result.id}>
														<Link to={{ pathname: '/movie-details/' }} >
		                            <MovieCards data={result}/>
	                          </Link>
		                    </Aux>
										)
                });
                console.log('movie_result', movie_result);
						}

        } else {
            movie_result = <div>Something is Wrong!</div>;
        }

				return (
						<Aux>
								<div>
										{movie_result}===
								</div>

                <ul>
                    <li><NavLink to="/posts/" exact activeClassName="my-active" activeStyle={{ color: '#fa923f', textDecoration: 'underline'}}>
                            Posts
                        </NavLink>
                    </li>
                </ul>
								<Switch>
		                <Route path="/posts" component={AsyncUser} />
		                <Route path="/movie-details" component={AsyncUser} />
		                <Route render={() => <h1>Not found</h1>}/>
		            </Switch>
            </Aux>
				);
		}
}

const mapDispatchToProps = dispatch => {
    return {
        posts: []
    }
};

export default Users;


